import React, { useCallback, useEffect, useState, useRef } from 'react';
import { Button, Skeleton, Typography } from '@mui/material';
import Search from '../../components/Search';
import DriveFileRenameOutlineOutlinedIcon from '@mui/icons-material/DriveFileRenameOutlineOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import { IconButton, CircularProgress, Box } from '@mui/material';
import ClassesDialog from '../../dialogs/ClassesDialog';
import Delete from '../../dialogs/Delete';
import { useNavigate } from 'react-router-dom';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import { get } from '../../services/api-services';
import CustomSnackbar from '../../modules/CustomSnackbar';
import debounce from 'lodash/debounce';
export default function Classes() {
  document.title = "Classes"
  const [course, setCourse] = useState({ name: '' });
  const [courseId, setCourseId] = useState('');
  const [classes, setClassses] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [open, setOpen] = useState(false);
  const [deleteOpen, setDeleteOpen] = useState(false);
  const containerRef = useRef(null);
  const [snackbar, setSnackbar] = React.useState({
    openSnackbar: false,
    message: "",
    severity: "",
  });
  const navigate = useNavigate()
  const onEditClass = async (value) => {
    setCourse({ name: value?.name });
    setCourseId(value?.id)
    setOpen(true);
  };
  const onDeleteClass = async (value) => {
    setCourseId(value?.id)
    setDeleteOpen(true);
  };
  const getClasses = async (search) => {
    setLoading(true)
    const filter = search ? JSON.stringify({
      where: {
        "name": { "like": `${search}`, "options": "i" }
      },
      order: "created DESC" // sort by createdAt in descending order
    }) : JSON.stringify({ order: "created DESC" })
    const res = await get(`courses?filter=${filter}`);
    if (res?.statusCode === 200) {
      setClassses(res?.data)
    }
    setLoading(false)
  }
  const handleSearch = (event) => {
    setSearch(event.target.value);
  };
  const delay = useCallback(debounce(getClasses, 300), []);
  useEffect(() => {
    delay(search)
  }, [search])
  return (
    <div className='flex-1  flex flex-col gap-4 '>
      <div className='flex flex-col static gap-4 md:justify-between md:flex-row '>
        <Typography variant='h1'> Classes</Typography>
        <div className='flex gap-2  justify-between'>
          <div className='w-1/2 sm:w-2/3'>
            <Search handleSearch={handleSearch} name='class' />
          </div>
          <Button
            variant='contained'
            className='h-[40px]'
            onClick={() => { setOpen(true) }}
          >Add Class</Button>
        </div>
      </div>
      <Box className='flex flex-col flex-1' ref={containerRef} sx={{ maxHeight: containerRef?.current?.offsetHeight, overflow: 'auto' }}>
        {loading ?
          <div className='flex-1 flex flex-col justify-between'>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
            <div className='flex gap-2 md:justify-between'>
              <Skeleton width={300} height={40} animation="wave" />
              <Skeleton height={40} width={115} animation="wave" />
            </div>
          </div> : classes?.length > 0 && !loading ?
            <List>
              {classes?.map((value) => {
                const labelId = `checkbox-list-secondary-label-${value?.id}`;
                return (
                  <ListItem
                    onClick={(params) => { navigate('subjects', { state: { courseId: value?.id } }) }}

                    key={value.id}
                    secondaryAction={
                      <div className='flex items-center gap-2'>
                        <IconButton onClick={(e) => { onEditClass(value); e.stopPropagation() }} sx={(theme) => ({
                          ':hover': {
                            color: theme.palette.action.hoverEdit,
                          },
                        })} className='size-0 md:size-1/2'>
                          <DriveFileRenameOutlineOutlinedIcon />
                        </IconButton>
                        <IconButton onClick={(e) => { onDeleteClass(value); e.stopPropagation() }} className='size-0 md:size-1/2'
                            sx={(theme) => ({
                              ':hover': {
                                color: theme.palette.action.hoverDelete,
                              },
                            })}
                          >

                          <DeleteOutlineOutlinedIcon />
                        </IconButton>
                      </div>
                    }
                    disablePadding
                  >
                    <ListItemButton>

                      <ListItemText id={labelId} className=' capitalize ' primary={`${value?.name}`} />
                    </ListItemButton>
                  </ListItem>
                );
              })}
            </List>
            : <Box className='flex justify-center items-center flex-1'><Typography variant='body1'>No classes yet.</Typography>   </Box>
        }
      </Box>
      <ClassesDialog open={open} setOpen={setOpen} courseId={courseId} setCourseId={setCourseId} getClasses={getClasses} course={course} setCourse={setCourse} snackbar={snackbar} setSnackbar={setSnackbar} />
      <Delete name="Class" deleteOpen={deleteOpen} setDeleteOpen={setDeleteOpen} id={courseId} setCourseId={setCourseId} getClasses={getClasses} endPoint='courses' snackbar={snackbar} setSnackbar={setSnackbar} />
      <CustomSnackbar
        openSnackbar={snackbar.openSnackbar}
        closeSnackbar={() =>
          setSnackbar({
            openSnackbar: false,
            message: "",
            severity: "",
          })
        }
        severity={snackbar.severity}
        message={snackbar.message}
      />
    </div>
  )
}
